import './styles.css'
import { useState, memo } from 'react'

const Card = ({ title, count, setCount }) => {
  console.log('card render', title)
  return (
    <div className="card">
      <h3>{title}</h3>
      <h1>{count}</h1>
      <div>
        <button
          onClick={() => {
            setCount((prevCount) => {
              const index = parseInt(title.replace('Card ', ''))
              const newState = [...prevCount]
              newState[index - 1]++
              return newState
            })
          }}
        >
          Tambah
        </button>
      </div>
    </div>
  )
}

const CardComp = memo(Card)

export default function App() {
  const [count, setCount] = useState([1, 1, 1])
  console.log('count', count)
  return (
    <div className="App">
      <CardComp title="Card 1" count={count[0]} setCount={setCount} />
      <CardComp title="Card 2" count={count[1]} setCount={setCount} />
      <CardComp title="Card 3" count={count[2]} setCount={setCount} />
    </div>
  )
}
